import React, { useEffect, useState } from "react";
import * as d3 from "d3";
import "./LineGraph.css";

const LineGraph = () => {
  const [data, setData] = useState([]);
  const [selectedCountry, setSelectedCountry] = useState("All");
  const [yAxisOption, setYAxisOption] = useState("avgProduction");

  useEffect(() => {
    d3.csv(process.env.PUBLIC_URL + "/datasets/line_graph_totalproduction_totalscores_by_countryandyear.csv").then((rows) => {
      console.log("CSV Rows Loaded:", rows);
      const parsed = rows.map((d) => ({
        country: d[""] || d["Country"] || d[Object.keys(d)[0]].trim(),
        year: +d[Object.keys(d)[1]],
        avgProduction: +d[Object.keys(d)[2]],
        avgScore: +d[Object.keys(d)[3]],
      }));
      setData(parsed);
    });
  }, []);

  const countries = Array.from(new Set(data.map((d) => d.country)));

  const filteredData =
    selectedCountry === "All"
      ? data
      : data.filter((d) => d.country === selectedCountry);

  const dataByYear = Array.from(
    d3.group(filteredData, (d) => d.year),
    ([year, values]) => ({
      year: +year,
      value: d3.mean(values, (d) => d[yAxisOption]),
    })
  ).sort((a, b) => a.year - b.year);

  const width = 600;
  const height = 300;
  const margin = { top: 20, right: 50, bottom: 40, left: 50 };

  const xScale = d3
    .scaleLinear()
    .domain(d3.extent(dataByYear, (d) => d.year))
    .range([margin.left, width - margin.right]);

  const yScale = d3
    .scaleLinear()
    .domain([
      d3.min(dataByYear, (d) => d.value) * 0.95,
      d3.max(dataByYear, (d) => d.value) * 1.05,
    ])
    .range([height - margin.bottom, margin.top]);

  return (
    <div className="line-graph-container">
      <h2>Average Score/Production Trends By Country</h2>
      <div className="controls">
        
        <label>
          Country:
          <select value={selectedCountry} onChange={(e) => setSelectedCountry(e.target.value)}>
            <option value="All">All</option>
            {countries.map((country) => (
              <option key={country} value={country}>{country}</option>
            ))}
          </select>
        </label>

        <label>
          Y-axis:
          <select value={yAxisOption} onChange={(e) => setYAxisOption(e.target.value)}>
            <option value="avgProduction">Average Production</option>
            <option value="avgScore">Average Score</option>
          </select>
        </label>
      </div>

      <svg viewBox={`0 0 ${width} ${height}`}
  preserveAspectRatio="xMinYMin meet"
  style={{ width: '100%', height: 'auto' }}>
        {/* X Axis */}
        {xScale.ticks().map((tick) => (
          <text
            key={tick}
            x={xScale(tick)}
            y={height - 5}
            fontSize="10"
            textAnchor="middle"
          >
            {tick}
          </text>
        ))}

        {/* Y Axis */}
        {yScale.ticks(5).map((tick) => (
          <text
            key={tick}
            x={margin.left - 10}
            y={yScale(tick)}
            fontSize="10"
            textAnchor="end"
            alignmentBaseline="middle"
            
          >
            {tick.toFixed(1)}
          </text>
        ))}

        {/* X-axis line */}
        <line
          x1={xScale(d3.min(dataByYear, d => d.year))}
          x2={xScale(d3.max(dataByYear, d => d.year))}
          y1={yScale.range()[0]}
          y2={yScale.range()[0]}
          stroke="#4e342e"
          strokeWidth="1"
        />

        

        {/* X-axis label */}
        <text
          x={width / 2}
          y={height + 20}
          fontSize="12"
          fill="#4e342e"
          textAnchor="middle"
          dy="-4"
        >
          Year
        </text>

        {/* Y-axis label */}
        <text
          transform={`rotate(-90)`}
          x={-height / 2}
          y="5"
          fontSize="12"
          fill="#4e342e"
          textAnchor="middle"
        >
          {yAxisOption === "avgProduction" ? "Average Production" : "Average Score"}
        </text>


        {/* Line Path */}
        <path
          fill="none"
          stroke="#4e342e"
          strokeWidth="2"
          d={d3
            .line()
            .x((d) => xScale(d.year))
            .y((d) => yScale(d.value))
            .curve(d3.curveMonotoneX)(dataByYear)}
        />
      </svg>
    </div>
  );
};

export default LineGraph;
